<template>
  <b-card-code title="Term of Use">
    <b-col md="12">
      <!-- <b-card-text>Description</b-card-text> -->
      <quill-editor
        v-model="term_of_use"
        :options="editorOption"
      />
    </b-col>
    <b-col
      cols="12"
      class="mt-50 text-right"
    >
      <b-button
        v-ripple.400="'rgba(255, 255, 255, 0.15)'"
        variant="primary"
        class="mr-1"
        @click="sendData"
      >
        Save Changes
      </b-button>
      <b-button
        v-ripple.400="'rgba(186, 191, 199, 0.15)'"
        variant="outline-secondary"
        @click="reset"
      >
        Reset
      </b-button>
    </b-col>
  </b-card-code>
</template>

<script>
import {
  BCol, BButton,
} from 'bootstrap-vue'
import BCardCode from '@core/components/b-card-code/BCardCode.vue'
import Ripple from 'vue-ripple-directive'
import { quillEditor, Quill } from 'vue-quill-editor'

const AlignStyle = Quill.import('attributors/style/align')
Quill.register(AlignStyle, true)
const Parchment = Quill.import('parchment')

class IndentAttributor extends Parchment.Attributor.Style {
  add(node, value) {
    if (value === 0) {
      this.remove(node)
      return true
    }
    return super.add(node, `${value}em`)
  }
}

const IndentStyle = new IndentAttributor('indent', 'text-indent', {
  scope: Parchment.Scope.BLOCK,
  whitelist: ['1em', '2em', '3em', '4em', '5em', '6em', '7em', '8em', '9em'],
})
Quill.register(IndentStyle, true)

export default {
  components: {
    quillEditor,
    BCardCode,
    BCol,
    BButton,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      editorOption: {
        modules: {
          toolbar: {
            container: [['bold', 'italic'], [{ align: '' }, { align: 'center' }, { align: 'right' }, { align: 'justify' }], ['link'], [{ size: ['small', false, 'large', 'huge'] }], [{ indent: '-1' }, { indent: '+1' }]],
          },
        },
      },
      search: '',
      value: [],
      term_of_use: '',
      term_of_useHeader: '',
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      this.$http.get('information').then(response => {
        response.data.forEach(element => {
          if (element.type === 'terms_of_use') {
            this.term_of_use = element.text
            this.term_of_useHeader = element.text
          }
        })
      })
    },
    sendData() {
      const formData = new FormData()
      formData.append('terms_of_use', this.term_of_use)
      this.$http.post('information/terms-of-use', formData).then(response => {
        if (response.data.success === true) {
          this.$bvToast.toast('Success Update Term Of Use', {
            title: 'Notification',
            toaster: 'b-toaster-top-center',
            variant: 'success',
            solid: false,
            appendToast: false,
          })
          this.getData()
        }
      })
    },
    reset() {
      this.term_of_use = this.term_of_useHeader
    },
  },
}
</script>
<style lang="scss">
@import '@core/scss/vue/libs/quill.scss';
</style>
